.root_wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  box-sizing: border-box;
  position: relative;
  width: 100%;
  min-height: 100vh;
  background: center / contain no-repeat url('./assets/calendar.png'),
    radial-gradient(gray -155%, black 95%);
  color: #fff;
  padding: 20px;
}

.section_wrapper {
  display: flex;
  width: 100%;
}

.calendar_wrapper {
  overflow-x: scroll;
}

.calendar_wrapper::-webkit-scrollbar {
  height: 5px;
  background-color: transparent;
  border-radius: 5px;
}
.calendar_wrapper::-webkit-scrollbar-thumb {
  background-color: steelblue;
  border-radius: 5px;
}
.calendar_wrapper::-webkit-scrollbar-thumb:hover {
  background-color: #253861;
}
